import React, { Component } from 'react';
import styled from 'styled-components';
import Circle from './Assets/circle.svg';

const Panel = styled.div`
	display: table;
	width: 600px;
	text-align: center;
	height: 100vh;
	float: left;
	position: absolute;
	right: 0;
	z-index: -1;
`;

const VerticalPanel = styled.div`
	display: table-cell;
	vertical-align: middle;
`;

function CircleImg(props) {
	const circleStyle = {
		position: 'absolute',
		minHeight: '150vh',
		height: '150vh',
		top: '-25%',
		right: '-100%'
	};
	
	return <img src={Circle} alt={"circle"} style={circleStyle}/>
}

function ContentPanel(props) {
	const Div = styled.div`
		color: white;
		z-index:1;
		position: absolute;
		top: 50%;
		right: 10%;
		border-left: 1px solid white;
		font-family: 'courier-std', 'sans-serif';
		font-weight: 400;
		font-style: normal;
	`;

	const H3 = styled.h3`
		padding: 16px 0 0 8px;
		margin: 0;
	`;

	const H4 = styled.h4`
		padding: 0;
		padding-left: 8px;
		margin: 0;
	`;

	return(
		<Div>
			<H3>{props.title}</H3>
			<br />
			<H4>{props.subtitle}</H4>
		</Div>
	);
}

class RightPanel extends Component {
	render() {
		const visualize = this.props.status;
		let outPanel;

		if (visualize) {
			outPanel = (
				<Panel>
					<VerticalPanel>
						<ContentPanel title="An intergalactic cat" subtitle="Computer science student" />
						<CircleImg />
					</VerticalPanel>
				</Panel>
			);
		}
		return (
			<div>
				{outPanel}
			</div>
		);
	}
}

export default RightPanel;
