import React, { Component } from 'react';
import styled from 'styled-components';

const Ul = styled.ul`
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	
	display: block;
`;

const Li = styled.li`
	display: inline
`;

const SocialMedia = styled.a`
	display: inline-block;
	color: black;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;

	&:hover {
		background-color: #111;
		color: white;
	}
`;

const Panel = styled.div`
	display: table;
	background-color: #bbb;
	width: 350px;
	text-align: center;
	height: 100vh;
	float: left;
`;

const VerticalPanel = styled.div`
	display: table-cell;
	vertical-align: middle;
`;

class LeftPanel extends Component {
	render() {
		return (
			<Panel>
				<VerticalPanel>
					<div>
						<img src={"https://avatars0.githubusercontent.com/u/25873364?v=3"} alt={"me"} width="100"/>
					</div>
					<div >
						<h1>{"Hello!"}</h1>
						<h3>{"Pedro Labrador"}</h3>
					</div>
					<div >
						<Ul>
							<Li>
								<SocialMedia href="https://github.com/PedroLabrador">Github</SocialMedia>
								<SocialMedia href="https://twitter.com/PedroLabradorp">Twitter</SocialMedia>
							</Li>
						</Ul>
					</div>
				</VerticalPanel>
			</Panel>
		);
	}
}

export default LeftPanel;
