import React, { Component } from 'react';
import LeftPanel from './Components/LeftPanel';
import CenterPanel from './Components/CenterPanel';
import RightPanel from './Components/RightPanel';

class App extends Component {
  constructor(props) {
		super(props);
		this.state = {
			visualizeRight: true
		};
		this.hideOrShowPanel = this.hideOrShowPanel.bind(this);
	}

	hideOrShowPanel(bool) {
		this.setState(state => ({
			visualizeRight: bool
		}));
	}

  render() {
    return (
      <div>
        <LeftPanel />
        <CenterPanel hideOrShowPanel={this.hideOrShowPanel} status={this.state.visualizeRight} />
        <RightPanel status={this.state.visualizeRight} />
      </div>  
    );
  }
}

export default App;
