import React, { Component } from 'react';
import styled from 'styled-components';
import Key from './Assets/key.svg';
import Dino from './Assets/dino.svg';
import Cat from './Assets/cat.png';

const Panel = styled.div`
	float: left;
	height: 100vh;
`;

const CentralPanel = styled.div`
	margin: 20px 0;
	display: table;
	text-align: center;
	width: calc(100vw - 350px);

	&:after {
		content: " ";
		display: block; 
		height: 0; 
		clear: both;
	}
`;

const Info = styled.div`
	float: left;
	color: #61DAFB;
	padding-left: 0px;
	font-family: 'bebas-neue', 'sans-serif';
	font-weight: 400;
	font-style: normal;
	font-size: 1.8em;
`;

function CatImg(props) {
	const catStyle = {
		float: 'left',
		marginTop: '-15px',
		paddingRight: '30px'
	};
	
	return <img src={Cat} alt={'cateye'} width={props.width} style={catStyle} />;
}

function Title(props) {
	const h1Style = {
		lineHeight: '250px',
		margin: '0 0 -30px 0',
		textShadow: "-1px -1px 0 #00C8FF, 1px -1px 0 #00C8FF, -1px 1px 0 #00C8FF, 1px 1px 0 #00C8FF",
		letterSpacing: '3px'
	}
	return <h1 style={h1Style}>{props.title}</h1>
}

function KeyImg(props) {
	const keyStyle = {
		transform: `rotate(${props.rotate})`,
		margin: `${props.decrease}`,
		padding: '0'
	};

	if (props.float) {
		keyStyle.float = 'left'
	}

	return <img src={Key} alt={"Key"} width={props.width} style={keyStyle} />
}

function DinoImg(props) {
	const Dimg = styled.img`
		float: right;
		margin: 0 10px 0 50px;
		animation: dino-rawr infinite 2s linear;

		@keyframes dino-rawr {
			0% {
				transform: rotate(0deg);
			}
			50% {
				transform: rotate(-25deg);
			}
			100% {
				transform: rotate(0deg);
			}
		}
	`;

	return <Dimg src={Dino} alt={"Dino"} width={props.width} />
}

function UpperPanel(props) {
	const upperPanelStyle = {
		margin: '35px'
	};

	const Ul = styled.ul`
		text-transform: uppercase;
		color: #61DAFB;
		font-size: 1em;
		overflow: auto;
		font-family: 'montserrat', 'sans-serif';
		font-weight: 400;
		font-style: normal;
	`;

	const A = styled.a`
		text-decoration: none;
		color: black;
	`;

	const Li = styled.li`
		float: left;
		padding: 12px;
		margin: 0 50px;

		&:hover ${A}{
			background-color: #61DAFB;
			color: white;
		};

		&:hover {
			background-color: #61DAFB;
			cursor: pointer;
		}
	`;

	const {view, visualize} = props;

	let main;

	if (!visualize) {
		main = (
			<Li>
				<A href={"/#"} onClick={() => view('Main', true)}>Main</A>
			</Li>
		)
	}

	return (
		<div style={upperPanelStyle}>
			<Ul>
				{main}
				<Li>
					<A href={"/#"} onClick={() => view('About', false)}>About me</A>
				</Li>
				<Li>
					<A href={"/#"} onClick={() => view('Blog', false)}>blog</A>
				</Li>
				<Li>
					<A href={"/#"} onClick={() => view('Portfolio', false)}>Portfolio</A>
				</Li>
			</Ul>
		</div>
	)
} 

function BottomPanel(props) {
	const A = styled.a`
		padding-left: 100px;
		font-family: 'montserrat', 'sans-serif';
		text-decoration: none;
		color: black;
	`;

	const Div = styled.div`
		border: 5px solid #61DAFB;
		float: left;
		padding: 10px;
		margin: 40px 0 0 -40px;

		&:hover ${A} {
			color: white;
		}

		&:hover {
			background-color: #61DAFB
			cursor: pointer;
		}
	`;

	return (
		<Div>
			<A href={props.link}>{props.text}</A>
		</Div>
	);
}

class CenterPanel extends Component {
	constructor(props) {
		super(props);
		this.state = {
			currentView: 'Main'
		};
		this.changeView = this.changeView.bind(this);
	}

	changeView(view, status) {
		console.log(view);
		this.props.hideOrShowPanel(status);
		this.setState(state => ({
			currentView: view
		}));
	}

	render() {
		const currentView = this.state.currentView;
		
		let central;

		if (currentView === 'Main') {
			central = (
				<div>
					<CentralPanel>
						<CatImg width="320" />
						<Info>
							<Title title="HI, i'M PEDRO" />
							<KeyImg width="200" rotate="-90deg" decrease="-150px 0 0 0" />
						</Info>
						<DinoImg width="150" />
					</CentralPanel>
					<div>
						<KeyImg width="150" rotate="180deg" decrease="-10px 0 0 50px" float={true} />
						<BottomPanel text="Go here!" link="/#" />
					</div>
				</div>
			);
		} else if (currentView === 'About') {
			central = (
				<div>
					<CentralPanel>
						<Info>
							<Title title="About" />
						</Info>
					</CentralPanel>
				</div>
			);
		} else if (currentView === 'Blog') {
			central = (
				<div>
					<CentralPanel>
						<Info>
							<Title title="Blog" />
						</Info>
					</CentralPanel>
				</div>
			);
		} else if (currentView === 'Portfolio') {
			central = (
				<div>
					<CentralPanel>
						<Info>
							<Title title="Portfolio" />
						</Info>
					</CentralPanel>
				</div>
			);
		}


		return (
			<div>
				<Panel>
					<UpperPanel view={this.changeView} visualize={this.props.status} />
					{central}
				</Panel>
			</div>
		);
	}
}

export default CenterPanel;